@import "../../_variables";

.dialogs {
    display: grid;
    grid-template-columns: 1fr 2fr;

    .dialogsList {
        border-right: 1px solid lightgray; }


    .sendMessageBlock {
        margin-top: 50px;
        padding: 10px;
        textarea {
            border: 1px solid lightgray;
            display: inline-block;
            vertical-align: middle;
            height: 60px;
            width: 300px;
            margin-right: 10px;
            padding: 5px; }
        button {
            display: inline-block;
            vertical-align: middle; } } }





